@import url(https://fonts.googleapis.com/css2?family=Simonetta:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  cursor: default;
}

.img-carousel {
  position: relative;
  min-height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: 1s fadeIn ease-in;
  padding: 1rem;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.image {
  position: relative;
  width: 100%;
  height: 20rem;
  border-radius: 5px;
}

.left-arrow,
.right-arrow {
  width: 3rem;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 2rem;
  color: black;
}
.slide {
  opacity: 0;
  transition: 1.5s ease-in;
}
.slide.active {
  opacity: 1;
  transition: 1.5s ease-in;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

*:root {
  --dark-green: #276955;
  --light-green: #41a58d;
  --main--font: "Simonetta", cursive;
}

.cards {
  background-color: var(--dark-green);
  color: white;
  text-align: center;
  padding: 1rem 0;
}

.center-text {
  -webkit-text-decoration: 4px underline;
          text-decoration: 4px underline;
  text-underline-offset: 0.5rem;
  padding-bottom: 1rem;
}

.card-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.shop-now-btn {
  background-color: var(--light-green);
  min-height: 3rem;
  width: 10rem;
  border: none;
  border-radius: 10px;
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

.shop-now-btn:hover {
  transform: scale(1.05);
}
.shop-now-btn:active {
  background-color: white;
  color: var(--light-green);
  transition: transform 0.05s ease-in-out;
  transform: scale(1);
}
.card {
  max-width: 30%;
  font-weight: bold;
  padding-bottom: 1rem;
}

@media screen and (max-width: 800px) {
  .card-wrapper {
    flex-direction: column;
  }
  .card {
    display: flex;
    flex-direction: column;
  }
}

.header-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 1rem;
  background-color: #276955;
  grid-gap: 2rem;
  gap: 2rem;
}
.bookcase {
  max-width: 90%;
}

.header-text {
  color: white;
  font-size: 5vw;
  text-decoration: underline;
  text-underline-offset: 0.3rem;
}

.text-wrapper {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 40rem) {
  .header-wrapper {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  }
  .bookcase {
    max-width: 60%;
  }
  .header-text {
    font-size: 3vw;
  }
}

.bookcard-wrapper {
  box-shadow: 2px 4px 10px 0 rgba(0, 0, 0, 0.2);
  text-align: center;
  min-height: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bookcard-image {
  padding-top: 1rem;
}
.description {
  font-size: 0.6rem;
  padding-bottom: 1rem;
}

.bookcard-title,
.bookcard-author {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.bookcard-title {
  max-width: 10rem;
}

.bookcard-author {
  max-width: 12rem;
}

.add-to-cart-btn {
  background-color: #41a58d;
  min-height: 3rem;
  width: 8rem;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: bold;
  color: white;
}

.add-to-cart-btn:hover {
  transform: scale(1.05);
}
.add-to-cart-btn:active {
  transition: transform 0.05s ease-in-out;
  transform: scale(1);
}

