*:root {
  --dark-green: #276955;
  --light-green: #41a58d;
  --main--font: "Simonetta", cursive;
}

.title,
.all-books,
.about-us,
.account {
  color: var(--dark-green);
  font-family: var(--main--font);
  font-size: 1.3rem;
}

.logo-wrapper:hover,
.all-books:hover,
.about-us:hover {
  text-decoration: underline;
  text-underline-offset: 0.2rem;
}
.nav {
  display: flex;
  border: 1px solid black;
  justify-content: space-evenly;

  align-items: center;
  max-width: 100%;
}

.logo-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 0.2rem;
  gap: 0.2rem;
}

.logo-img {
  width: 2.5rem;
}
.searchbar-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 1rem;
  width: 30%;
  padding: 0.5rem;
  border: 1.5px solid black;
  border-radius: 15px;
}
.searchbar-wrapper img {
  width: 1.5rem;
}

.searchbar {
  border-radius: 10px;
  min-height: 1.3rem;
  width: 100%;
  border: none;
  outline: none;
}
.shoppingcart-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  width: 2rem;
}
.shoppingcart-container:hover,
.account:hover {
  transform: scale(1.05);
  text-decoration: underline;
}
.shoppingcart {
  width: 2rem;
}
.active {
  text-underline-offset: 0.2rem;
  text-decoration: underline;
}

.nav-cart-items::after {
  position: absolute;
  text-align: center;
  left: 65%;
  bottom: 50%;
  background-color: var(--dark-green);
  color: white;
  content: attr(data-count);
  border-radius: 100%;
  padding: 0.1rem;
  width: 1rem;
}

.dropdown {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.7rem;
}
.dropdown:hover {
  background-color: rgba(177, 177, 177, 0.432);
  border-radius: 10px;
}
.account-wrapper {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}
.account {
  position: relative;
  height: 3rem;
  width: 2rem;
}
.account-text {
  margin: 0.3rem 0 0.3rem 0;
}

.dropdown-content {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f9f9f9;
  position: absolute;
  top: 7%;
  min-width: 12rem;
  height: 7rem;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border-radius: 10px;
}

.log-in,
.create-account,
.signout {
  width: 9rem;
  height: 2rem;
  border-radius: 10px;
  font-weight: bold;
}
.log-in:hover,
.create-account:hover {
  transform: scale(1.05);
}
.shoppingcart-container:active,
.account:active,
.log-in:active,
.create-account:active {
  transition: transform 0.05s ease-in-out;
  transform: scale(1);
}
.log-in,
.signout {
  border: none;
  background-color: var(--dark-green);
  color: white;
}
.signout {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}
.signout-img {
  padding-top: 0.15rem;
  height: 1rem;
}
.create-account {
  border: 1px solid var(--dark-green);
  background-color: white;
  color: var(--dark-green);
}

.show {
  display: flex;
}

*:root {
  --dark-green: #276955;
  --light-green: #41a58d;
  --main--font: "Simonetta", cursive;
}

.modal-open {
  overflow: hidden;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
  display: flex;
  justify-content: flex-end;
}

.menu {
  overflow: auto;
  position: absolute;
  right: -50rem;
  width: clamp(15rem, 30rem, 45vw);
  height: 100vh;
  background-color: white;
  animation: slide 0.3s forwards;
}

@keyframes slide {
  100% {
    right: 0;
  }
}
.modal-title {
  margin: 0;
  background-color: var(--dark-green);
  font-size: 2rem;
  color: white;
  font-family: var(--main--font);
  padding: 1rem;
}

.close {
  width: 3rem;
  height: 1rem;
}
.close:hover {
  cursor: pointer;
}
.close:before,
.close:after {
  position: absolute;
  top: 2.7%;
  right: 10%;
  content: " ";
  height: 30px;
  width: 5px;
  background-color: white;
}
.close:before {
  transform: rotate(45deg);
}
.close:after {
  transform: rotate(-45deg);
}
.modal-img {
  height: clamp(2rem, 20rem, 15rem);
}
.empty-cart {
  display: flex;
  height: 60vh;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  color: var(--dark-green);
  font-weight: bold;
}

.shop-books-btn {
  background-color: var(--light-green);
  min-height: 3rem;
  width: 10rem;
  border: none;
  border-radius: 10px;
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

.shop-books-btn:hover {
  transform: scale(1.05);
}
.shop-books-btn:active {
  transition: transform 0.05s ease-in-out;
  transform: scale(1);
}
.cartInfo {
  height: 65%;
  padding-bottom: 2rem;
  overflow: auto;
}

.bookInfo {
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding-bottom: 1rem;
}
.bookCartImage {
  height: 6.5rem;
  padding-left: 1rem;
}
.bookCartInfo {
  font-size: 0.8rem;
}
.quantity-container {
  display: flex;
  align-items: center;
  grid-gap: 0.3rem;
  gap: 0.3rem;
  padding-bottom: 0.5rem;
}
.quantity {
  text-align: center;
  width: 1rem;
}

.decrement,
.increment,
.removeCartBtn,
.checkOut {
  background-color: #41a58d;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: bold;
  color: white;
}
.removeCartBtn {
  width: 5rem;
  min-height: 2rem;
}

.checkOut-Container {
  padding-top: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.checkOut {
  width: 14rem;
  min-height: 3rem;
}
.decrement:hover,
.increment:hover,
.removeCartBtn:hover,
.checkOut:hover {
  transform: scale(1.05);
}

.decrement:active,
.increment:active,
.removeCartBtn:active,
.checkOut:active {
  transition: transform 0.05s ease-in-out;
  transform: scale(1);
}

.total {
  margin: 0;
  padding: 0 1.5rem 0 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid black;
  box-shadow: 0 -2px 4px 0 rgba(0, 0, 0, 0.062);
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

.createAccount-header {
  color: var(--dark-green);
}
.createAccount-form {
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}
.createAccount-Input {
  height: 1.5rem;
  width: 15rem;
}
.username,
.email,
.password,
.confirm-Password {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.createAccount-btn {
  background-color: var(--dark-green);
  color: white;
  height: 2rem;
  border: none;
  border-radius: 5px;
  font-weight: bold;
}

footer {
  padding-top: 2rem;
  color: #41a58d;
  font-weight: bold;
  padding-top: 3rem;
}

.footer-wrapper {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: #41a58d;
  font-weight: bold;
  padding-bottom: 1rem;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.copyright {
  text-align: center;
}

